.set1,
.set2 {
  left: 50%;
  position: absolute;
  -webkit-transition: opacity 0.35s, transform 0.35s;
  -moz-transition: opacity 0.35s, transform 0.35s;
  -ms-transition: opacity 0.35s, transform 0.35s;
  -o-transition: opacity 0.35s, transform 0.35s;
